import { TextMeasure } from './measure';

export const SYMBOL_DEFAULT_FONT = 'Arial';
export const SYMBOL_DEFAULT_SIZE = '11pt';
const SYMBOL_SPACE_OFFSET = 10;
const RegExpChinese = /.*[\u4e00-\u9fa5]+.*$/;

export const SYMBOL_UNIT_WIDTH = 24;

export const CHINESE_COUNTING_THOUSAND = 'chineseCountingThousand';

export const getSymbolRightSpace = (
  align: string,
  listSymbol: string,
  symbolStyle: React.CSSProperties,
): number => {
  const symbolSize = (symbolStyle.fontSize as string) || SYMBOL_DEFAULT_SIZE;
  const symbolWidth = TextMeasure.measureTextWidth(
    String(listSymbol),
    symbolStyle.fontFamily || SYMBOL_DEFAULT_FONT,
    symbolSize,
  );
  let result;
  if (
    RegExpChinese.test(listSymbol) ||
    align === 'right' ||
    listSymbol.length === 1
  ) {
    // 无序列表、中文有序、罗马有序保持8px距离
    result = 8;
  } else {
    result = SYMBOL_UNIT_WIDTH - (symbolWidth % SYMBOL_UNIT_WIDTH);
    if (symbolSize === SYMBOL_DEFAULT_SIZE && result === SYMBOL_UNIT_WIDTH) {
      result -= SYMBOL_SPACE_OFFSET;
    }
  }
  return result;
};
